}

/* Header occupies top 30px and sprawls left across chart component */
.editor-header-wrapper {
  position: absolute;
  top: -40px;
  right: 0;
  width: 206%;
  height: 40px;
  pointer-events: none;
}

/* Next is a kludge: if Editor isn't a child of Sibyl,
this class resets header width */
.parentless-header {
  width: 100%;
}
.chart-preset-choices-div {
  width: 50%;
}
.parentless-choices {
  width: 100%;
}

/* Outer wrapper for form */
.editor-outer-wrapper {
  position: absolute;
  width: 95%;
  height: 100%;
  margin-left: 3%;
}

/* stylelint-enable */
/* CHART DATA fold elements: textarea... */
.chartdata-textarea {
  font-family: EconSansCndReg;
  font-size: 16px;
  line-height: 20px;
  height: 200px;
  width: 98%;
  -moz-tab-size: 30;
       tab-size: 30;
  overflow: auto;
  resize: none;
}
.chartdata-textarea::-webkit-input-placeholder {
  white-space: pre-line;
  position: relative;
}
.chartdata-textarea::-moz-placeholder {
