}
/* Context choices wrapper: left half of Editor header */
.chart-context-choices-div {
  position: absolute;
  top: 0;
  left: 2px;
  width: 49.75%;
  height: 100%;
  pointer-events: all;
}

/* Reset button wrapper: right half of Editor header */
.chart-reset-wrapper {
  /* NOTE: I' ve hidden the reset button for now... */
  display: none;
  position: absolute;
  top: 0;
  left: 50%;
