}

/* Class to hide blob controls on Datafold */
.blobs-hidden {
  display: none;
}

/* Classes for footnote fieldset in Datafold */
.footnote-fieldset {
  width: 95%;
  /*overflow-y: auto;*/
}
.footnote-add-div {
  width: 100%;
  margin-top: 10px;
}
.footnote-add-span {
  width: 50px;
  height: 10px;
  padding: 5px 0px;
  background-color: #fff;
  float: right;
  text-align: center;
  cursor: pointer;
  opacity: 0.5;
}
.footnote-add-span:hover {
  background-color: #f55;
}
.footnote-add-span:active {
  font-weight: bold;
  opacity: 1;
}
.footnote-div {
  background-color: none;
  width: 100%;
  height: 20px;
  position: relative;
  margin-top: 5px;
}
.footnote-div-label {
  background-color: none;
  position: absolute;
  top: 0;
  left: 0;
  /*float: left;*/
  height: 100%;
  width: 5%;
  line-height: 2em;
  text-align: right;
}
.footnote-input {
  position: absolute;
  right: 0;
  height: 100%;
  width: 93%;
  border-width: 0;
  padding: 0;
}

/*  Chart outer wrapper nestles round child SVG object */

.silver-chartwrapper {
  display: inline-block;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  transition-duration: 0.5s, 0.5s;
  transition-property: height, width;
}

.svg-wrapper {
  width: 100%;
  height: 100%;
}

.chart-zoom-050 {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}

.chart-zoom-1 {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.chart-zoom-150 {
  -webkit-transform: scale(1.95);
          transform: scale(1.95);
}

.chart-zoom-175 {
  -webkit-transform: scale(2.275);
          transform: scale(2.275);
}

.chart-zoom-250 {
  -webkit-transform: scale(2.5);
          transform: scale(2.5);
}

/*
NOTE: I'm not sure that this component needs any css
since we'll probably wind up with hard svg element props
*/

.d3-key-text {
  font-family: 'OfficinaSanITC-Book';
  font-size: 7px;
  fill: #000;
  stroke: none;
}

/*  This CSS file defines default styling for D3-generated axes...
      Some elements are simply set 'off' (display: none)
      Others (ticks and text) are set to be invisible by default
        then inline styling will impose styles determined by prefs file(s)
*/

.axis-group {
  fill: none;
}
/* Ignore domain path, which I explicitly remove */

/* 'Hide' ticks and text */
/* font and font-size are (hopefully) 'close enough' to avoid disruption */
.axis-group line {
  fill: none;
  stroke: black;
  stroke-width: 0;
}
.axis-group text {
  font-size: 6.5px;
  fill: none;
  stroke: none;
}

/* Start with main size-and-presents wrapper */
.size-wrapper {
  position: relative;
  grid-column: 2;
  font-size: 14px;
}
.size-wrapper > * {
  margin-right: 5px;
}
.width-input {
  height: 23px;
  /* Width increased to allow for Free Exchange 245.08 */
  width: 70px;
  min-width: 36px;
  font-weight: 600;
}
.silver-label.x-label {
  font-size: 18px;
  color: var(--tertiary-text);
}
.height-input {
  width: 55px;
  height: 23px;
  min-width: 36px;
  font-weight: 600;
}
.size-error {
  color: var(--grey-four);
  background-color: var(--alert-red);
  border-color: var(--alert-red);
}

.recommended-span {
  width: 40px;
  height: 24px;
  line-height: 19px;
  border: none;
  font-size: 13px;
  background-color: var(--grey-two);
  color: white;
  font-weight: 600;
  cursor: pointer;
  padding: 0 5px 0 0;
  text-align: center;
}
.silver-label.recommended-label {
  color: var(--button);
  font-size: 13px;
  font-weight: 600;
}
.recommended-span:hover {
  background-color: var(--grey-one);
}
.recommended-span:active {
  background-color: var(--grey-four);
  color: var(--grey-one);
}
.recommended-span:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 1100px) {
  .silver-label.recommended-label {
    position: absolute;
    right: 0;
    top: -20px;
  }
}
/* Preset styling */
.silver-button.preset-button {
  margin-right: 10px;
  background: var(--button);
  color: white;
  font-weight: 600;
  max-width: 230px;
  transition: background 0.2s;
}
.silver-button.preset-button .main-preset,
.silver-button.preset-button .sub-preset {
  display: inline-block;
  text-transform: none;
}
.silver-button.preset-button .main-preset::first-letter {
  text-transform: uppercase;
}

.silver-button.preset-button:hover {
  background: var(--secondary-text);
  border: 1px solid var(--secondary-text);
}
.silver-button.preset-button.active {
  background: var(--secondary-text);
  border: 1px solid var(--secondary-text);
}
.silver-button.preset-button > svg {
  margin-bottom: 1px;
  margin-left: 2px;
}

.preset-menu {
  z-index: 1;
  position: absolute;
  top: 102%;
  left: 0;
  background: white;
  border: 1px solid var(--button);
  min-width: 550px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.preset-menu:after,
.preset-menu:before {
  content: '';
  display: inline-block;
  position: absolute;
}
.preset-menu:before {
  border: 8px solid transparent;
  border-bottom-color: var(--button);
  left: auto;
  left: 59px;
  top: -16px;
}
.preset-menu:after {
  border: 7px solid transparent;
  border-bottom-color: white;
  left: auto;
  left: 60px;
  top: -13px;
}
.preset-menu-type {
  -webkit-flex-basis: 34%;
          flex-basis: 34%;
  margin-left: -1px;
}
.preset-menu-type:last-child .preset-menu-list .preset-menu-item {
  border-right: none;
}
.preset-menu-display {
  font-weight: 600;
  padding-bottom: 5px;
  margin-top: 5px;
  margin-bottom: -1px;
  border-bottom: 1px solid var(--secondary-text);
  text-align: center;
}
.silver-button.preset-menu-item {
  display: block;
  width: 100%;
  padding: 10px;
  text-transform: none;
  margin-bottom: -1px;
}
.silver-button.preset-menu-item.active {
