/* Global colour definitions for Sibyl's layout */

input,
select {
  font-size: 13px;
}

:root {
  --highlight: #69c5b7;
  --grey-one: #586a74;
  --grey-two: #758d9a;
  --grey-three: #b7c6cf;
  --grey-four: #e9edf0;
  --alert-red: #db444b;
  --font-stack: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
}

/* Silver Bullet outer wrapper */
.silver-bullet {
  display: grid;
  max-width: 1400px;
  grid-row-gap: 10px;
  grid-column-gap: 30px;
  /* min-height: 700px; */
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
  font-family: var(--font-stack);
  font-size: 14px;
  grid-template-columns: 280px auto;
  grid-template-rows: 0 160px auto 160px;
}

@media (min-height: 1100px) {
  .silver-bullet {
    height: calc(100vh - 10px);
  }
}

.editor-wrapper {
  display: grid;
  grid-column: 1/3;
  grid-row: 2/5;
  grid-row-gap: 10px;
  grid-column-gap: 30px;
  grid-template-columns: 280px auto;
  grid-template-rows: 40px 40px 100px auto 120px 40px;
}

/* Immediate children of Silver Bullet */
.sync-and-share-wrapper {
  grid-column: 1/3;
  grid-row: 1;
}

.folds-wrapper {
  margin: 0;
  padding: 0;
  grid-column: 1;
  grid-row: 1/6;
  grid-auto-rows: minmax(300px, auto);
}

.size-and-preset-wrapper {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-column: 2;
  grid-row: 1;
  grid-template-columns: 40% 33%;
}

.panels-outer-wrapper {
  pointer-events: none;
  grid-column: 2;
  grid-row: 1;
  position: relative;
}

.strings-wrapper {
  grid-column: 2;
  grid-row: 2/6;
  display: grid;
  grid-template-rows: 110px auto 120px;
  grid-template-columns: 45% 45%;
  grid-column-gap: 10%;
}

.footer-wrapper {
  border-top: 1px solid var(--grid-line);
  padding-top: 5px;
  display: grid;
  grid-row: 6;
  grid-column: 1/3;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  grid-template-columns: 60% 30%;
  grid-template-rows: 1;
}

/* Chart wrapper */

.chart-wrapper {
  box-sizing: border-box;
  overflow: auto;
  padding: 20px;
  border: 1px solid var(--button);
  grid-column: 2;
  grid-row: 3/4;
  min-height: 500px;
  grid-auto-rows: minmax(300px, auto);
}

.svg-wrapper {
  background-color: none;
}

